
/* 반응형 스타일 */

@media (max-width: 768px) {
    .text-white {
        font-size: 6vw; /* 작은 화면에서 폰트 크기를 줄임 */
        text-shadow: 0.3vw 0.3vw 0.6vw rgba(0, 0, 0, 0.5); /* 텍스트 그림자 크기 조정 */
        -webkit-text-stroke: 0.3vw black; /* 텍스트 테두리 크기 조정 */
    }
    
    p {
        font-size: 3vw; /* 작은 화면에서 폰트 크기를 줄임 */
    }

    h2 {
        font-size: 4vw; /* 작은 화면에서 폰트 크기를 줄임 */
    }

    .item {
        width: 30vw; /* 작은 화면에서 이미지 크기를 줄임 */
        height: 50vh; /* 작은 화면에서 이미지 높이를 줄임 */
        background-size: contain; /* 작은 화면에서 이미지 크기 조정 */
    }
    
    .item.expanded {
        width: 70vw; /* 작은 화면에서 확장된 이미지 크기를 조정 */
    }
}

@font-face {
    font-family: 'PartialSansKR-Regular';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/PartialSansKR-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face{
    font-family:'DNFBitBitv2';
    font-style:normal;font-weight:400;src:url('//cdn.df.nexon.com/img/common/font/DNFBitBitv2.otf')format('opentype')}

h1 {
    font-family: 'PartialSansKR-Regular'; /* 폰트 패밀리 적용 */
}

.text-white {
    font-size: 100px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    -webkit-text-stroke: 2px black;
}

p{
    font-family:'DNFBitBitv2';
}
h2{
    font-family: 'DNFBitBitv2';
}

.group {
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
}

.item {
    width: 15vw;
    height: 75vh;
    background-position: center;
    background-size: 75vh;
    margin: 1vw;
    border-radius: 3vw;
    display: inline-block;
    cursor: pointer;
    transition: width 0.5s ease; /* 너비 변경에 대한 전환 효과 추가 */
  }
  
.item.expanded {
    width: 42vw; /* 확장된 상태의 너비 */
}


/* 링크 */

.fan-site-button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 5px;
  background-color: #7ec8e3;
  color: #ffffff;
  text-decoration: none;
  border: none;
  cursor: pointer;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.fan-site-button:hover {
  background-color: #5eb1d7;
  transform: translateY(-2px);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.2);
}